<template>
    <div class="contact">
        <vue-headful :title="$t('contact.meta-title')" :description="$t('contact.meta-description')"/>

        <section class="hero is-contact">
            <div class="hero-body">
                <div class="container">
                    <h2 class="title is-size-3 has-text-centered is-unselectable">{{ $t('contact.title') }}</h2>
                    <p class="subtitle has-text-centered is-unselectable">{{ $t('contact.subtitle') }}</p>
                </div>
            </div>
        </section>

        <section class="hero is-lighter">
            <div class="hero-body">
                <div class="container">
                    <h3 class="title is-size-3 has-text-centered is-unselectable">{{ $t('contact.why.title') }}</h3>

                    <div class="columns is-centered">

                        <div class="column is-8-tablet has-text-identation is-size-5-5">
                            <p>
                                <span>{{ $t('contact.why.part1') }}</span>&nbsp;
                            </p>
                            <p>
                                <span>{{ $t('contact.why.part2') }}</span>&nbsp;
                                <span>{{ $t('contact.why.part3') }}</span>&nbsp;
                            </p>
                            <p>{{ $t('contact.why.part8') }}</p>
                        </div>
                    </div>
                </div>
            </div>
        </section>

        <section class="hero ">
            <div class="hero-body">
                <div class="container">
                    <h3 class="title is-size-3 has-text-centered is-unselectable">{{ $t('contact.methods.title') }}</h3>

                    <div class="columns is-centered is-multiline">
                        <div class="column is-half-tablet is-one-third-desktop">
                            <b-button class="is-light" tag="router-link" to="/donation" 
                                @click="$gtag.event('How-to-contact', {'event_category': 'Clicks', 'event_label': 'donation'})">
                                <article class="media">
                                    <figure class="media-left">
                                        <b-icon class="has-text-danger is-size-2"  icon="heart"/>
                                    </figure>

                                    <div class="media-content">
                                        <h5 class="is-size-4 has-text-weight-semibold">{{ $t('contact.methods.donate') }}</h5>
                                        <p class="is-size-5 has-text-grey">{{ $t('contact.methods.donate-info') }}</p>
                                    </div>
                                </article>
                            </b-button>
                        </div>
                    </div>

                    <div class="columns is-centered is-multiline">
                        <div class="column is-half-tablet is-one-third-desktop">
                            <b-button class="is-light" tag="a" href="https://github.com/coodesker/coodesker-desktop/issues" target="_blank" rel="noopener"
                                @click="$gtag.event('How-to-contact', {'event_category': 'Clicks', 'event_label': 'Github'})">
                                <article class="media">
                                    <figure class="media-left">
                                        <ResponsiveImage :src="require('@/assets/media/contact/GitHub.svg')" width="80px" height="80px" maxWidth="80px" maxHeight="80px"/>
                                    </figure>

                                    <div class="media-content">
                                        <h5 class="is-size-4 has-text-weight-semibold">Github</h5>
                                        <p class="is-size-5 has-text-grey">{{ $t('contact.methods.github-info') }}</p>
                                    </div>
                                </article>
                            </b-button>
                        </div>

                        <div class="column is-half-tablet is-one-third-desktop">
                            <b-button class="is-light" @click="openEmailDialog()">
                                <article class="media">
                                    <figure class="media-left">
                                        <ResponsiveImage :src="require('@/assets/media/contact/Gmail.svg')" width="80px" height="80px" maxWidth="80px" maxHeight="80px"/>
                                    </figure>

                                    <div class="media-content">
                                        <h5 class="is-size-4 has-text-weight-semibold">{{ $t('contact.methods.email') }}</h5>
                                        <p class="is-size-5 has-text-grey">{{ $t('contact.methods.email-info') }}</p>
                                    </div>
                                </article>
                            </b-button>
                        </div>

                        <div class="column is-half-tablet is-one-third-desktop">
                            <b-button class="is-light" @click="openQQDialog()">
                                <article class="media">
                                    <figure class="media-left">
                                        <ResponsiveImage :src="require('@/assets/media/contact/QQ.svg')" width="80px" height="80px" maxWidth="80px" maxHeight="80px"/>
                                    </figure>

                                    <div class="media-content">
                                        <h5 class="is-size-4 has-text-weight-semibold">QQ</h5>
                                        <p class="is-size-5 has-text-grey">{{ $t('contact.methods.qq-info') }}</p>
                                    </div>
                                </article>
                            </b-button>
                        </div>
                    </div>

                    <b-modal :active.sync="isEmailModalActive" trap-focus aria-role="dialog" aria-modal :width="500" scroll="keep"
                        @close="$gtag.event('How-to-contact', {'event_category': 'Clicks', 'event_label': 'Email hide'})">
                        <div class="box has-text-centered" style="padding: 40px">
                            <h2 class="title">{{ $t('contact.methods.email-reveal.title') }}</h2>
                            <p class="subtitle">{{ $t('contact.methods.email-reveal.subtitle') }}</p>

                            <div class="has-email">
                                <code>coodesker@gmail.com</code>
                                <b-button type="is-primary" inverted @click="copyEmail()">{{ $t('home.copy') }}</b-button>
                            </div>
                        </div>
                    </b-modal>

                    <b-modal :active.sync="isQQModalActive" trap-focus aria-role="dialog" aria-modal :width="500" scroll="keep"
                        @close="$gtag.event('How-to-contact', {'event_category': 'Clicks', 'event_label': 'QQ hide'})">
                        <div class="box has-text-centered" style="padding: 40px">
                            <h2 class="title">{{ $t('contact.methods.qq-reveal.title') }}</h2>
                            <p class="subtitle">{{ $t('contact.methods.qq-reveal.subtitle') }}</p>
                           
                            <figure class="image">
                                <ResponsiveImage :src="require('@/assets/media/contact/QQQRCode.png')" width="260px" height="260px" maxWidth="260px" maxHeight="260px"/>
                            </figure>
                            
                            <div class="has-email">
                                <code>729581290</code>
                                <b-button type="is-primary" inverted @click="copyQQ()">{{ $t('home.copy') }}</b-button>
                            </div>
                        </div>
                    </b-modal>

                    
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    import ResponsiveImage from "@/components/ResponsiveImage.vue";

    export default {
        components: {
            ResponsiveImage
        },

        data() {
            return {
                isEmailModalActive: false,
                isQQModalActive: false
            }
        },

        computed: {
            age() {
                var now = new Date();
                var then = new Date(1993, 9, 1);

                var year = now.getFullYear() - then.getFullYear();
                var month = now.getMonth() - then.getMonth();

                if (month < 0)
                    year--;

                return year;
            }
        },

        methods: {
            openEmailDialog() {
                this.isEmailModalActive = true;
                this.$gtag.event('How-to-contact', {'event_category': 'Clicks', 'event_label': 'Email show'});
            },
            copyEmail() {
                this.$gtag.event('Copy', {'event_category': 'Clicks', 'event_label': 'Email'});

                this.$copyText('coodesker@gmail.com').then((e) => {
                    this.$buefy.toast.open({
                        duration: 500,
                        message: this.$t('contact.methods.email-reveal.copied'),
                        position: 'is-bottom',
                        type: 'is-success'
                    });
                }, (e) => {
                    console.log('It was not possible to copy the email.', e);

                    this.$buefy.toast.open({
                        duration: 500,
                        message: this.$t('contact.methods.email-reveal.copied-error'),
                        position: 'is-bottom',
                        type: 'is-danger'
                    });

                    this.$gtag.exception({'description': e, 'fatal': false});
                });
            },
            openQQDialog() {
                this.isQQModalActive = true;
                this.$gtag.event('How-to-contact', {'event_category': 'Clicks', 'event_label': 'QQ show'});
            },
            copyQQ() {
                this.$gtag.event('Copy', {'event_category': 'Clicks', 'event_label': 'QQ'});

                this.$copyText('729581290').then((e) => {
                    this.$buefy.toast.open({
                        duration: 500,
                        message: this.$t('contact.methods.qq-reveal.copied'),
                        position: 'is-bottom',
                        type: 'is-success'
                    });
                }, (e) => {
                    console.log('It was not possible to copy the QQ.', e);

                    this.$buefy.toast.open({
                        duration: 500,
                        message: this.$t('contact.methods.qq-reveal.copied-error'),
                        position: 'is-bottom',
                        type: 'is-danger'
                    });

                    this.$gtag.exception({'description': e, 'fatal': false});
                });
            }
        },
    };
</script>

<style lang="scss" scoped>
    @import "@/mixins/colors.scss";

    //Background of the hero panel.
    .hero.is-contact {
        background-image: linear-gradient($contact, $contact-light);
    }
    
    //Fills up the space.
    .column {
        height: 100%;
        padding: 0.75rem 0.5rem;
    }

    .column .button {
        white-space: normal;
        border-radius: 5px;
        padding: 0.75rem;
        width: 100%;
        height: 100%;
    }

    //Reduces the margin of the icon of media element.
    figure.media-left{
        margin-right: 0.5rem;
    }

    //Removes the negative margin of the button.
    article span.icon {
        margin-left: 0px !important;
        margin-right: 0px !important;
    }

    //Adds the new paragraph identation.
    .has-text-identation {
        text-indent: 2rem;
    }

    .is-vertical-centered {
        align-self: center;
        align-content: center;
    }

    .is-size-5-5 {
        font-size: 1.20rem;
    }

    //Email block and button to copy.
    .has-email {
        display: inline-flex;
        font-size: 1.1em;
        color: white;
        background: #5cadd5;
        padding: calc(0.75rem - 1px); 
        line-height: 1.5rem;
        border-radius: 4px;
        align-items: center;
        -webkit-overflow-scrolling: touch;
        overflow-x: auto;
        white-space: pre;
        word-wrap: normal;
    }

    //Makes the code inside the div.has-email blend in.
    .has-email code {
        background-color: transparent;
        color: currentColor;
        font-size: 1em;
        padding: 0 0 0 0.75rem;
    }

    .has-email button {
        font-family: inherit;
        line-height: 1.5rem;
        margin: 0 0 0 1rem;
    }
</style>

<style lang="scss">
    //Increase the size of the custom icon.
    .has-icon-large .icon {
        mask-size: 3.5rem;
    }

    //Makes inner element of the buttons ocupy the whole space.
    button > span, a > span {
        width: 100%;
    }
</style>